
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Card from 'vux/src/components/card/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Clipboard from "clipboard";
import { mapState, mapGetters } from "vuex";
import img1 from "../assets/img/1.jpg";
import img2 from "../assets/img/2.jpg";
import img3 from "../assets/img/3.jpg";
import ffffff from "../assets/img/ffffff.jpg";
const baseList = [
{
url: "javascript:",
img: img1,
title: "送你一朵fua"
},
{
url: "javascript:",
img: img2,
title: "送你一辆车"
},
{
url: "javascript:",
img: img3,
title: "送你一次旅行"
}
];

export default {
components: {
XHeader,
Tabbar,
TabbarItem,
Card,
Swiper,
GroupTitle,
SwiperItem,
Panel,
Group,
XButton
},
created() {},
data() {
return {
tabIndex: 1,
viewHome: true,
demo01_list: baseList,
demo01_index: 0,
type: "1",
copyText: "我是可以复制的内容，啦啦啦啦",
copyBtn: null, //存储初始化复制按钮事件
list: [
{
src: ffffff,
title: "标题一",
desc:
"由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。",
url: "/msg"
},
{
src: ffffff,
title: "标题二",
desc:
"由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。",
url: {
path: "/msg",
replace: false
},
meta: {
source: "来源信息",
date: "时间",
other: "其他信息"
}
}
],
footer: {
title: "查看更多",
url: "/about"
}
};
},
computed: {
...mapState({
orders: "orders"
}),
...mapGetters({
waitOrdersNum: "waitOrdersNum",
dealOrders: "dealOrders"
})
},
mounted() {
this.copyBtn = new Clipboard(".copy");
},
methods: {
clickTab() {
console.log("in");
},

copyToClipboard() {
let _this = this;
let clipboard =
_this.copyBtn == null ? new Clipboard(".copy") : _this.copyBtn;
clipboard.on("success", function() {
_this.$vux.toast.show({
text: "复制成功！"
});
_this.copyBtn = null;
clipboard.destroy();
});
clipboard.on("error", function() {
_this.$vux.toast.show({
type: 'warn',
text: "复制失败，请手动选择复制！"
});
_this.copyBtn = null;
clipboard.destroy();
});
}
}
};



